.gpt3__whatgpt3 {
  background: var(--color-footer);
  margin: 4rem 6rem;
  padding: 4rem;
  display: flex;
  flex-direction: column;

  /* ff 3.6+ */
  background: -moz-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* ie 10+ */
  background: -ms-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* global 92%+ browsers support */
  background: radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gpt3__whatgpt3-feature {
  margin-bottom: 4rem;
}

.gpt3__whatgpt3-feature .gpt3__features-container__feature {
  display: flex;
  justify-content: space-between;
}

.gpt3__whatgpt3-feature .gpt3__features-container__feature-text {
  max-width: 700px;
}

.gpt3__whatgpt3-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4rem;
}

.gpt3__whatgpt3-heading h1 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 800;
  font-family: var(--font-family);
  max-width: 510px;
}

.gpt3__whatgpt3-heading p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: var(--color-subtext);
  cursor: pointer;
}

.gpt3__whatgpt3-heading p:is(:hover, :focus) {
  opacity: 0.8;
}

.gpt3__whatgpt3-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 2rem;
}

.gpt3__whatgpt3-container .gpt3__features-container__feature {
  flex-direction: column;
}

@media screen and (max-width: 1100px) {
  .gpt3__whatgpt3 {
    padding: 4rem;
    margin: 4rem 6rem;
  }
  .gpt3__whatgpt3-heading {
    flex-direction: column;
    align-items: flex-start;
  }
  .gpt3__whatgpt3-heading h1 {
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 850px) {
  .gpt3__whatgpt3-feature .gpt3__features-container__feature {
    flex-direction: column;
  }

  .gpt3__whatgpt3-heading h1 {
    font-size: 28px;
    line-height: 35px;
  }

  .gpt3__whatgpt3-container {
    flex-direction: column;
  }
}

@media screen and (max-width: 700px) {
  .gpt3__whatgpt3 {
    margin: 4rem 4rem;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__whatgpt3 {
    margin: 4rem 2rem;
  }
  .gpt3__whatgpt3-heading h1 {
    font-size: 22px;
    line-height: 28px;
  }
}

@media screen and (max-width: 400px) {
  .gpt3__whatgpt3 {
    padding: 2rem;
  }
}
