.gpt3__brand {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
}

.gpt3__brand div {
  margin-top: 1rem;

  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.gpt3__brand div img {
  width: clamp(80px, 80px + 2vw, 120px);
  cursor: pointer;
}
